.sidebar {
	background-color: #f74881;
	border-radius: 20px;
	box-shadow: 0 0 7px #ad2955;

	grid-area: sidebar;
	margin-right: 10px;
}

.sidebar__list {
	padding: 20px;

	margin-bottom: 100px;
}

.sidebar__item {
	margin-bottom: 10px;
}

a {
	text-decoration: none;
}

.sidebar__item a {
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	color: #fff;
	font-size: 18px;
	cursor: pointer;
	letter-spacing: 1.5px;
}

h2 {
	text-align: center;
}

.sidebar__friends {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 0px 10px 0px 10px;
}
